<template>
  <main class="ribbon-bg">
    <AdminNavBar />

    <section class="mt-page-header py-5">
      <div class="container mt-md-4">
        <div class="row text-center">
          <h1 class="page-header-title">Published Posts</h1>
          <p class="page-header-text mb-0">
            Create beautiful contents
          </p>
        </div>
      </div>

      <main class="py-4">
        <div class="container">
          <div class="row justify-content-center">

            <div class="col-lg-12 mt-lg-n10">
              <div class="card">
                <div class="card-header bg-green">
                  <div class="small text-white font-weight-500">
                    Published Articles
                  </div>
                </div>
                <div class="card-body">
                  <table class="table table-hover table-sm text-center align-middle">
                    <thead>
                      <tr>
                        <th scope="col">SN</th>
                        <th scope="col" class="w-30">Title</th>
                        <th scope="col">Date</th>
                        <th scope="col">Author</th>
                        <th scope="col">View</th>
                        <th scope="col">Popular</th>
                        <th scope="col">Action 3 </th>
                      </tr>
                    </thead>
                    <tbody id="postlist">
                      <tr v-for="article in articles" v-bind:key="article.id">
                        <th scope="row">{{article.count}}</th>
                        <td class="text-start">{{article.title}}</td>
                        <td>{{article.date}}</td>
                        <td>{{article.author}}</td>
                        <td class="small">
                          <router-link :to="/articles/+article.slug+ '-' + article.id" target="_blank">
                            <i class="bi bi-eye-fill"></i>
                          </router-link>
                        </td>
                        <td class="small">
                          <a @click="markUnpopular(article.id)" v-show="article.mark_id" href="javascript:void(0)">Unmark</a>
                          <a @click="markPopular(article.id)" v-show="!article.mark_id" href="javascript:void(0)">Mark</a>
                        </td>
                        <td class="small">
                          <a @click="unpublish(article.id)" href="javascript:void(0)">Unpublish</a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>

          </div>
        </div>
      </main>

    </section>

  </main>

</template>

<script>
  import {db} from '../../components/firebaseInit'
  import AdminNavBar from '../../components/admin/AdminNavBar'
  export default {
    name: 'Dashboard',
    components: {
      AdminNavBar
    },
    data() {
      return {
        counter: 1,
        articles: [],
        marked: null
      }
    },
    created() {
      db.collection('posts').where("status", "==", 3).orderBy('timestamp', 'desc').get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          if(doc.data().popularity == 0){
            this.marked = false
          }else{
            this.marked = true
          }
          const data = {
            'id': doc.id,
            'title': doc.data().title,
            'date': doc.data().date,
            'author': doc.data().author,
            'popularity': doc.data().popularity,
            'slug': doc.data().slug,
            'mark_id': this.marked,
            'count': this.counter
          }
          this.counter++
          this.articles.push(data)
        })
      })
    },
    methods:{
      watcher(){
        db.collection('posts').where("status", "==", 3).orderBy('timestamp', 'desc')
          .onSnapshot((querySnapshot) => {
          this.articles = []
          this.counter = 1
          querySnapshot.forEach((doc) => {
            const data = {
              'id': doc.id,
              'title': doc.data().title,
              'date': doc.data().date,
              'author': doc.data().author,
              'popularity': doc.data().popularity,
              'mark_id': this.marked,
              'count': this.counter
            }
            this.counter++
            this.articles.push(data)
          })
        })
      },
      markPopular(id){
        if(confirm("Are you Sure to mark Popular?")){
          db.collection("posts").doc(id).update({
            popularity: 1
          })
          .then(() => {
            alert("Marked Popular!")
            this.marked = true
            this.watcher()
          })
          .catch((e) => {
            console.log(e)
          });
        }
      },
      markUnpopular(id){
        if(confirm("Are you Sure to mark unpopular?")){
          db.collection("posts").doc(id).update({
            popularity: 0
          })
          .then(() => {
            alert("Marked Unpopular!")
            this.marked = false
            this.watcher()
          })
          .catch((e) => {
            console.log(e)
          });
        }
      },
      unpublish(id){
        if (confirm("On Unpublished, Artcile will sent to Drafts. Are you Sure?")) {
          db.collection("posts").doc(id).update({
              status: 5,
              popularity: 0,
            })
            .then(() => {
              alert("Article Unpublished and sent to Drafts");
              this.watcher();
            })
            .catch((e) => {
              console.log(e);
            });
        }
      }
    }
  }
</script>

<style scoped>

</style>