<template>

  <main>
    <NavBar />

    <section class="py-3">
      <main class="pt-4">
        <div class="container">
          <div class="row">

            <div class="col-lg-8 mt-lg-n10">
              <div v-for="article in articles" v-bind:key="article.id" class="collection-item">
                <div class="card mb-4">
                  <router-link :to="/articles/+article.slug+ '-' + article.id">
                    <img :src="article.image" class="card-img-top" alt="Image">
                  </router-link>
                  <div class="card-body px-4 border-top">
                    <div class="d-flex align-items-center">
                      <div>
                        <div class="mb-3 article-head">
                          <h2 class="mb-0 blog-title">
                            <router-link :to="/articles/+article.slug+ '-' + article.id">
                              {{article.title}}
                            </router-link>
                          </h2>
                          <div class="small text-muted my-2">
                            {{article.date}} by
                            <a href=""> {{article.author}} </a>
                          </div>
                        </div>

                        <div>
                          <p>
                            {{article.shortIntro}}
                          </p>
                        </div>
                        <router-link :to="/articles/+article.slug+ '-' + article.id" class="btn btn-primary">
                          Read more
                          <i class="bi bi-arrow-right"></i>
                        </router-link>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="col-lg-4 mt-lg-n5">
              <aside class="position-sticky latest-guides">
                <div class="card">
                  <div class="card-header bg-green">
                    <div class="text-white font-weight-500">
                      Popular Articles
                    </div>
                  </div>
                  <div v-for="popular in popularLinks" v-bind:key="popular.id" class="list-group list-group-flush small mb-0 border-bottom">
                    <router-link :to="/articles/+popular.slug+ '-' + popular.id" class="list-group-item list-group-item-action">
                      <div class="font-weight-500 small">{{popular.title}}</div>
                      <div class="text-xs text-muted">{{popular.date}}</div>
                    </router-link>
                  </div>
                  <div class="card-body">
                    <a class="btn btn-green-soft btn-block font-weight-500" href="#">
                      View All
                    </a>
                  </div>
                </div>
              </aside>
            </div>
          </div>

        </div>
      </main>
    </section>

    <Footer />
  </main>
</template>

<script>
  import {
    db
  } from '../components/firebaseInit'
  import NavBar from '../components/NavBar'
  import Footer from '../components/Footer'

  export default {
    name: 'home',
    components: {
      NavBar,
      Footer
    },
    data() {
      return {
        articles: [],
        popularLinks: []
      }
    },
    created() {
      db.collection('posts').where("status", "==", 3).orderBy('timestamp', 'desc').get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = {
            'id': doc.id,
            'slug': doc.data().slug,
            'title': doc.data().title,
            'date': doc.data().date,
            'author': doc.data().author,
            'image': doc.data().imageurl,
            'shortIntro': doc.data().shortIntro,
          }
          this.articles.push(data)
        })
      })

      db.collection('posts').where("popularity", "==", 1).orderBy('timestamp', 'desc').get().then((querySnapshot) => {
        this.loading = false
        querySnapshot.forEach((doc) => {
          const data = {
            'id': doc.id,
            'slug': doc.data().slug,
            'title': doc.data().title,
            'date': doc.data().date,
            'shortIntro': doc.data().shortIntro.split(" ", 15).join(" ").concat("......"),
          }
          this.popularLinks.push(data)
        })
      })
    }
  }
</script>

<style scoped>
  .page-header-text,
  .text-white-50 {
    color: hsla(0, 0%, 100%, .5) !important;
    font-size: 1.15rem;
  }

  .blog-preview-img {
    width: 7rem;
    max-width: 100%;
  }

  h2 {
    font-size: 1.42rem;
  }

  .blog-title a:hover {
    cursor: pointer;
    text-decoration: underline !important;
  }

  .article-head a,
  .novus-footer a {
    text-decoration: none;
  }
  .border-top{
    border-top: 1px solid #efeff0;
  }
</style>