<template>

  <main>
    <NavBar />

    <section class="py-3">
      <main class="pt-4">

        <div class="container">
          <div class="row">
            <div class="col-lg-8 mt-lg-n10">
              <ArticleView v-if="loaded" :title=article.title :date=article.date :author=article.author :tag1=article.tag1 :tag2=article.tag2 :content=article.content />
              <ArticleAuthor v-if="loaded" :name=author.name :intro=author.intro :lkd_link=author.linkedin_link :avatar=author.avatar />
            </div>

            <div class="col-lg-4 mt-lg-n5">
              <aside class="position-sticky latest-guides">
                <div class="card">
                  <div class="card-header bg-green">
                    <div class="text-white font-weight-500">
                      Popular Articles
                    </div>
                  </div>
                  <div v-for="popular in popularLinks" v-bind:key="popular.id" class="list-group list-group-flush small mb-0 border-bottom">
                    <router-link :to="/articles/+popular.slug+ '-' + popular.id" class="list-group-item list-group-item-action">
                      <div class="font-weight-500 small">{{popular.title}}</div>
                      <div class="text-xs text-muted">{{popular.date}}</div>
                    </router-link>
                  </div>
                  <div class="card-body">
                    <a class="btn btn-green-soft btn-block font-weight-500" href="#">
                      View All
                    </a>
                  </div>
                </div>
              </aside>
            </div>
          </div>
        </div>
      </main>
    </section>

    <Footer />
  </main>
</template>

<script>
  import {
    db
  } from '../components/firebaseInit'
  import NavBar from '../components/NavBar'
  import Footer from '../components/Footer'
  import ArticleView from '../components/ArticleView'
  import ArticleAuthor from '../components/ArticleAuthor'

  export default {
    name: 'home',
    components: {
      NavBar,
      Footer,
      ArticleView,
      ArticleAuthor
    },
    data() {
      return {
        article: [],
        author: [],
        popularLinks: [],
        article_slug: this.$route.params.article_slug,
        loaded: false,
      }
    },
    beforeRouteEnter(to, from, next) {
      const link = to.params.article_slug.substring(1).split("-")
      db.collection('posts').doc(link[link.length - 1]).get().then((querySnapshot) => {
        if (querySnapshot.exists) {
          next()
        } else {
          next({
            path: "/"
          })
        }
      })
    },
    created() {
      const link = this.article_slug.substring(1).split("-")
      //let authorName
      db.collection('posts').doc(link[link.length - 1]).get().then((doc) => {
        const data = {
          'id': doc.id,
          'title': doc.data().title,
          'date': doc.data().date,
          'author': doc.data().author,
          'authorid': doc.data().authorid,
          'content': doc.data().content,
        }
        this.article = data
        this.loaded = true
        db.collection('authors').doc(data.authorid).get().then((doc) => {
          if (doc.exists) {
            const data1 = {
              'id': doc.id,
              'name': doc.data().name,
              'intro': doc.data().intro,
              'fb_link': doc.data().fb_link,
              'linkedin_link': doc.data().linkedin_id,
              'linkedin_name': doc.data().linkedin_name,
              'avatar': doc.data().avatar,
            }
            this.author = data1
            this.loaded = true
          }
        })
      })

      db.collection('posts').where("popularity", "==", 1).orderBy('timestamp', 'desc').get().then((querySnapshot) => {
        this.loading = false
        querySnapshot.forEach((doc) => {
          const data = {
            'id': doc.id,
            'slug': doc.data().slug,
            'title': doc.data().title,
            'date': doc.data().date,
            'shortIntro': doc.data().shortIntro.split(" ", 15).join(" ").concat("......"),
          }
          this.popularLinks.push(data)
        })
      })

    }
  }
</script>