//import Vue from 'vue';
import {
  createRouter,
  createWebHistory
} from 'vue-router'
//import firebase from 'firebase'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Article from '../views/Article.vue'
import Login from '../views/admin/Login.vue'
import Dashboard from '../views/admin/Dashboard.vue'
import Published from '../views/admin/Published.vue'
import Create from '../views/admin/Create.vue'
import Drafts from '../views/admin/Drafts.vue'
import Spiderman from '../views/admin/Spiderman.vue'


const routes = [{
    path: '/home',
    component: Home
  },
  {
    path: '/about',
    component: About
  },
  {
    path: '/articles/:article_slug',
    component: Article
  },
  {
    path: '/',
    component: Login,
  },
  {
    path: '/admin/dashboard',
    component: Dashboard,
  },
  {
    path: '/admin/published',
    component: Published,
  },
  {
    path: '/admin/create',
    component: Create,
  },
  {
    path: '/admin/drafts',
    component: Drafts,
  },
  {
    path: '/admin/spiderman',
    component: Spiderman
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router