<template>
  <main class="ribbon-bg">
    <AdminNavBar />

    <section class="mt-page-header py-5">
      <div class="container mt-md-4">
        <div class="row text-center">
          <h1 class="page-header-title">Draft Articles</h1>
          <p class="page-header-text mb-0">
            Create beautiful contents
          </p>
        </div>
      </div>

      <main class="py-4">
        <div class="container">
          <div class="row justify-content-center">

            <div class="col-lg-12 mt-lg-n10 mb-3">
              <div class="card">
                <div class="card-header bg-green">
                  <div class="small text-white font-weight-500">
                    Draft Articles
                  </div>
                </div>
                <div class="card-body">
                  <table class="table table-hover table-sm text-center align-middle">
                    <thead>
                      <tr>
                        <th scope="col">SN</th>
                        <th scope="col" class="w-30">Title</th>
                        <th scope="col">Date</th>
                        <th scope="col">Author</th>
                        <th scope="col">View Article</th>
                        <th scope="col">Publish</th>
                        <th scope="col">Send to edit</th>
                      </tr>
                    </thead>
                    <tbody id="postlist">
                      <tr v-for="article in articles" v-bind:key="article.id">
                        <th scope="row">{{article.count}}</th>
                        <td class="text-start">{{article.title}}</td>
                        <td>{{article.date}}</td>
                        <td>{{article.author}}</td>
                        <td class="small">
                          <router-link :to="/articles/+article.slug+ '-' + article.id" target="_blank">
                            <i class="bi bi-eye-fill"></i>
                          </router-link>
                        </td>
                        <td class="small">
                          <a @click="publishArticle(article.id)" href="javascript:void(0)">
                            <i class="bi bi-file-arrow-up-fill"></i>
                          </a>
                        </td>
                        <td class="small">
                          <a @click="sendToEdit(article.id)" href="javascript:void(0)">
                            <i class="bi bi-cursor-fill"></i>
                          </a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>

            <div class="col-lg-12 mt-lg-n10 mb-3">
              <div class="card">
                <div class="card-header bg-green">
                  <div class="small text-white font-weight-500">
                    Archive Articles
                  </div>
                </div>
                <div class="card-body">
                  <table class="table table-hover table-sm text-center align-middle">
                    <thead>
                      <tr>
                        <th scope="col">SN</th>
                        <th scope="col" class="w-30">Title</th>
                        <th scope="col">Date</th>
                        <th scope="col">Author</th>
                        <th scope="col">View</th>
                        <th scope="col">Send to edit</th>
                        <th scope="col">Delete Forever</th>
                      </tr>
                    </thead>
                    <tbody id="postlist">
                      <tr v-for="article in archives" v-bind:key="article.id">
                        <th scope="row">{{article.count}}</th>
                        <td class="text-start">{{article.title}}</td>
                        <td>{{article.date}}</td>
                        <td>{{article.author}}</td>
                        <td>
                          <router-link :to="/articles/+article.slug+ '-' + article.id" target="_blank">
                            <i class="bi bi-eye-fill"></i>
                          </router-link>
                        </td>
                        <td class="small">
                          <a @click="sendToEdit(article.id)" href="javascript:void(0)">
                            <i class="bi bi-cursor-fill"></i>
                          </a>
                        </td>
                        <td class="small">
                          <a @click="deleteForever(article.id, article.imageName)" href="javascript:void(0)">
                            <i class="bi bi-trash-fill"></i>
                          </a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>

            <div class="col-lg-12 mt-lg-n10 mb-3">
              <div class="card">
                <div class="card-header bg-green">
                  <div class="small text-white font-weight-500">
                    Unpublished Articles
                  </div>
                </div>
                <div class="card-body">
                  <table class="table table-hover table-sm text-center align-middle">
                    <thead>
                      <tr>
                        <th scope="col">SN</th>
                        <th scope="col" class="w-30">Title</th>
                        <th scope="col">Date</th>
                        <th scope="col">Author</th>
                        <th scope="col">View</th>
                        <th scope="col">Send to edit</th>
                        <th scope="col">Delete Forever</th>
                      </tr>
                    </thead>
                    <tbody id="postlist">
                      <tr v-for="article in unpublished" v-bind:key="article.id">
                        <th scope="row">{{article.count}}</th>
                        <td class="text-start">{{article.title}}</td>
                        <td>{{article.date}}</td>
                        <td>{{article.author}}</td>
                        <td>
                          <router-link :to="/articles/+article.slug+ '-' + article.id" target="_blank">
                            <i class="bi bi-eye-fill"></i>
                          </router-link>
                        </td>
                        <td class="small">
                          <a @click="sendToEdit(article.id)" href="javascript:void(0)">
                            <i class="bi bi-cursor-fill"></i>
                          </a>
                        </td>
                        <td class="small">
                          <a @click="deleteForever(article.id, article.imageName)" href="javascript:void(0)">
                            <i class="bi bi-trash-fill"></i>
                          </a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>

          </div>
        </div>
      </main>

    </section>

  </main>

</template>

<script>
  import {db, storage} from '../../components/firebaseInit'
  import AdminNavBar from '../../components/admin/AdminNavBar'
  export default {
    name: 'Dashboard',
    components: {
      AdminNavBar
    },
    data() {
      return {
        counter: 1,
        articles: [],
        archives: [],
        unpublished: [],
      }
    },
    created() {
      db.collection('posts').where("status", "==", 2).orderBy('timestamp', 'desc').get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = {
            'id': doc.id,
            'title': doc.data().title,
            'date': doc.data().date,
            'author': doc.data().author,
            'slug': doc.data().slug,
            'count': this.counter
          }
          this.counter++
          this.articles.push(data)
        })
      })

      db.collection('posts').where("status", "==", 4).orderBy('timestamp', 'desc').get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = {
            'id': doc.id,
            'title': doc.data().title,
            'date': doc.data().date,
            'author': doc.data().author,
            'imageName': doc.data().imagename,
            'count': this.counter
          }
          this.counter++
          this.archives.push(data)
        })
      })

      db.collection('posts').where("status", "==", 5).orderBy('timestamp', 'desc').get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = {
            'id': doc.id,
            'title': doc.data().title,
            'date': doc.data().date,
            'author': doc.data().author,
            'imageName': doc.data().imagename,
            'count': this.counter
          }
          this.counter++
          this.unpublished.push(data)
        })
      })

    },
    methods: {
      watcher(){
        db.collection('posts').where("status", "==", 2).orderBy('timestamp', 'desc')
          .onSnapshot((querySnapshot) => {
          this.articles = []
          this.counter = 1
          querySnapshot.forEach((doc) => {
            const data = {
              'id': doc.id,
              'title': doc.data().title,
              'date': doc.data().date,
              'author': doc.data().author,
              'popularity': doc.data().popularity,
              'mark_id': this.marked,
              'count': this.counter
            }
            this.counter++
            this.articles.push(data)
          })
        })
      },
      archiveWatcher(){
        db.collection('posts').where("status", "==", 4).orderBy('timestamp', 'desc')
          .onSnapshot((querySnapshot) => {
          this.archives = []
          this.counter = 1
          querySnapshot.forEach((doc) => {
            const data = {
              'id': doc.id,
              'title': doc.data().title,
              'date': doc.data().date,
              'author': doc.data().author,
              'imageName': doc.data().imagename,
              'count': this.counter
            }
            this.counter++
            this.archives.push(data)
          })
        })
      },
      unpublishWatcher(){
        db.collection('posts').where("status", "==", 5).orderBy('timestamp', 'desc')
          .onSnapshot((querySnapshot) => {
          this.unpublished = []
          this.counter = 1
          querySnapshot.forEach((doc) => {
            const data = {
              'id': doc.id,
              'title': doc.data().title,
              'date': doc.data().date,
              'author': doc.data().author,
              'imageName': doc.data().imagename,
              'count': this.counter
            }
            this.counter++
            this.unpublished.push(data)
          })
        })
      },
      publishArticle(id){
        if (confirm("Are you Sure to PUBLISH?")) {
          db.collection("posts").doc(id).update({
            status: 3
          })
          .then(() => {
            alert("Article Published");
            this.watcher()
          })
          .catch((e) => {
            console.log(e)
          })
        }
      },
      sendToEdit(id){
        if (confirm("Are you Sure to send the article to CREATE?")) {
          db.collection("posts").doc(id).update({
            status: 1
          })
          .then(() => {
            alert("Article Sent to Create Folder");
            this.watcher()
          })
          .catch((e) => {
            console.log(e)
          })
        }
      },
      deleteForever(id, img){
        if (confirm("Are you Sure to DELETE Article & Image")) {
          let imageRef = storage.ref().child('images/' + img)
          imageRef.delete()
          db.collection("posts").doc(id).delete()
          .then(() => {
            alert("Article & Image deleted forever");
            this.archiveWatcher()
            this.unpublishWatcher()
          })
          .catch((e) => {
            console.log(e)
          })
        }
      }
    }
  }
</script>

