<template>
  <main class="ribbon-bg">
    <AdminNavBar />

    <section class="mt-page-header py-5">
      <div class="container mt-md-4">
        <div class="row text-center">
          <h1 class="page-header-title">Spiderman</h1>
          <p class="page-header-text mb-0">
            Create beautiful contents
          </p>
        </div>
      </div>

      <main class="py-4">
        <div class="container">
          <div class="row justify-content-center">

            <div class="col-lg-6 mt-lg-n10">
              <div class="card">
                <div class="card-header bg-green">
                  <div class="small text-white font-weight-500">
                    Add Authors
                  </div>
                </div>
                <div class="card-body">
                  <form>
                    <div class="mb-3 row g-3 align-items-center">
                      <div class="col-12">
                        <input class="form-control form-control-sm" v-model="name" placeholder="Name" />
                      </div>
                      <div class="col-12">
                        <input class="form-control form-control-sm" v-model="email" placeholder="Email" />
                      </div>
                      <div class="col-12">
                        <input class="form-control form-control-sm" v-model="intro" placeholder="Intro" />
                      </div>
                      <div class="col-12">
                        <input class="form-control form-control-sm" v-model="fb_id" placeholder="Facebook Profile ID" />
                      </div>
                      <div class="col-12">
                        <input class="form-control form-control-sm" v-model="linkedin_id" placeholder="LinkedIn Profile ID" />
                      </div>
                      <div class="col-12">
                        <input class="form-control form-control-sm" v-model="avatar" placeholder="Avatar Link" />
                      </div>
                      <div class="col-12">
                        <button type="button" class="btn btn-primary me-2" @click="addAuthor">Add</button>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>

            <div class="col-lg-6 mt-lg-n10">
              <div class="card">
                <div class="card-header bg-green">
                  <div class="small text-white font-weight-500">
                    Authors
                  </div>
                </div>
                <div class="card-body">
                  <table class="table table-hover table-sm text-center align-middle">
                    <thead>
                      <tr>
                        <th scope="col">SN</th>
                        <th scope="col">Name</th>
                        <th scope="col">View</th>
                        <th scope="col">Delete</th>
                      </tr>
                    </thead>
                    <tbody id="postlist">
                      <tr v-for="author in authorList" v-bind:key="author.name">
                        <th scope="row">{{author.count}}</th>
                        <td class="text-start">{{author.name}}</td>
                        <td class="small">
                          <a @click="openModal(author.id)">
                            <i class="bi bi-eye-fill"></i>
                          </a>
                        </td>
                        <td class="small">
                          <a @click="deleteAuthor(author.id)" href="javascript:void(0)">
                            <i class="bi bi-trash-fill"></i>
                          </a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>

          </div>
        </div>
      </main>

    </section>

    <div v-if="showModal">
      <transition name="fade">
        <div class="modal-mask" style="display: block">
          <div class="modal-dialog" role="document">
            <div class="card modal-content">
              <div class="card-header bg-green">
                <div class="small text-white font-weight-500">
                  Edit Article
                </div>
              </div>
              <div class="card-body">
                <form>
                  <div class="mb-3 row g-3 align-items-center">
                    <div class="col-12">
                      <label class="form-label">Name</label>
                      <input class="form-control form-control-sm" :value="authorDetails.name" />
                    </div>
                    <div class="col-12">
                      <label class="form-label">Email</label>
                      <input class="form-control form-control-sm" :value="authorDetails.email" />
                    </div>
                    <div class="col-12">
                      <label class="form-label">Intro</label>
                      <input class="form-control form-control-sm" :value="authorDetails.intro" />
                    </div>
                    <div class="col-12">
                      <label class="form-label">FB Profile ID</label>
                      <div class="input-group">
                        <span class="input-group-text" id="basic-addon3">https://facebook.com/</span>
                        <input class="form-control form-control-sm" :value="authorDetails.fb_id">
                      </div>
                    </div>
                    <div class="col-12">
                      <label class="form-label">LinkedIn Profile ID</label>
                      <div class="input-group">
                        <span class="input-group-text" id="basic-addon3">https://linkedin.com/</span>
                        <input class="form-control form-control-sm" :value="authorDetails.linkedin_id">
                      </div>
                    </div>
                    <div class="col-12">
                      <button type="button" class="btn btn-green-soft" @click="closeModal">Close</button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </transition>
    </div>

  </main>
</template>

<script>
  import {
    db
  } from '../../components/firebaseInit'
  import AdminNavBar from '../../components/admin/AdminNavBar'
  export default {
    name: 'Spiderman',
    components: {
      AdminNavBar
    },
    data() {
      return {
        counter: 1,
        name: null,
        email: null,
        intro: null,
        fb_id: null,
        linkedin_id: null,
        avatar: null,
        authorList: [],
        authorDetails: {
          name: null,
          email: null,
          intro: null,
          fb_id: null,
          linkedin_id: null
        },
        showModal: false,
      }
    },
    created() {
      db.collection('authors').get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            const data = {
              'id': doc.id,
              'name': doc.data().name,
              'count': this.counter
            }
            this.authorList.push(data)
            this.counter++
          })
        })
        .catch((e) => {
          console.log(e);
        })
    },
    methods: {
      watcher() {
        db.collection('authors').onSnapshot((querySnapshot) => {
          this.authorList = []
          this.counter = 1
          querySnapshot.forEach((doc) => {
            const data = {
              'id': doc.id,
              'name': doc.data().name,
              'count': this.counter
            }
            this.counter++
            this.authorList.push(data)
          })
        })
      },
      addAuthor() {
        if (this.name == "" || this.email == "" || this.intro == "" || this.fb_id == "" || this.linkedin_id == "") {
          alert("Enter Data first!")
        } else {
          db.collection('authors').add({
              name: this.name,
              email: this.email,
              intro: this.intro,
              fb_id: this.fb_id,
              linkedin_id: this.linkedin_id,
              avatar: this.avatar
            })
            .then(() => {
              alert('Data Updated successfully');
              this.watcher();
            })
            .catch((e) => {
              console.log(e);
            })
        }
      },
      openModal(id) {
        this.showModal = true
        document.getElementsByTagName('body')[0].style = 'overflow: hidden;'
        db.collection('authors').doc(id).get().then((doc) => {
          this.authorDetails.name = doc.data().name
          this.authorDetails.email = doc.data().email
          this.authorDetails.intro = doc.data().intro
          this.authorDetails.fb_id = doc.data().fb_id
          this.authorDetails.linkedin_id = doc.data().linkedin_id
        })
      },
      closeModal() {
        this.showModal = false
        document.getElementsByTagName('body')[0].style = ''
      },
      deleteAuthor(id) {
        db.collection("authors").doc(id).delete().then(() => {
            alert("Successfully Deleted");
            this.watcher()
          })
          .catch((e) => {
            console.log(e);
          })
      },
    },
  }
</script>


<style scoped>
  .modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.5s ease;
  }

  .fade-enter-from,
  .fade-leave-to {
    opacity: 0;
  }
</style>