<template>
  <main class="ribbon-bg">
    <AdminNavBar />

    <section class="mt-page-header py-5">
      <div class="container mt-md-4">
        <div class="row text-center">
          <h1 class="page-header-title">Admin Login Portal</h1>
          <p class="page-header-text mb-0">
            Create, Edit, Update, Delete, Manage, CMS
          </p>
        </div>
      </div>

      <main class="py-4">
        <div class="container">
          <div class="row justify-content-center">

            <div class="col-lg-6 mt-lg-n10">
              <div class="card">
                <div class="card-header bg-green">
                  <div class="small text-white font-weight-500">
                    Admin Login
                  </div>
                </div>
                <div class="card-body">
                  <form>
                    <div class="col-12 mb-3">
                      <input type="email" class="form-control" v-model="email" placeholder="Email address">
                    </div>
                    <div class="col-12 mb-3">
                      <input type="password" class="form-control" v-model="password" placeholder="Password">
                    </div>
                    <button @click="login" class="btn btn-primary" type="submit">Login</button>
                  </form>
                  <div class="mt-3" id="wrongalert"></div>
                </div>
              </div>
            </div>

          </div>
        </div>
      </main>
    </section>

    <Footer />
  </main>
</template>

<script>
  
  import firebase from 'firebase'
  import AdminNavBar from '../../components/admin/AdminNavBar'
  import Footer from '../../components/Footer'

  export default {
    components: {
      AdminNavBar,
      Footer
    },
    data(){
      return{
        email: '',
        password: ''
      }
    },
    created() {
      firebase.auth().onAuthStateChanged((user) => {
        if (user) {
          this.$router.push('/admin/dashboard')
        } else {
          this.$router.push('/admin')
        }
      })
    },
    methods:{
      login:function(e){
        firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(() =>{
          this.$router.push('/admin/dashboard')
        })
        .catch(() => {
          document.getElementById("wrongalert").innerHTML = '<div class="alert alert-danger" role="alert">Oh Snap! Bad Credentials!</div>'
      })
        e.preventDefault()
      }
    }
  }
</script>

