<template>
  <main class="ribbon-bg">
    <AdminNavBar />

    <section class="mt-page-header py-5">
      <div class="container mt-md-4">
        <div class="row text-center">
          <h1 class="page-header-title">Create Articles</h1>
          <p class="page-header-text mb-0">
            Create beautiful contents
          </p>
        </div>
      </div>

      <main class="py-4">
        <div class="container">
          <div class="row justify-content-center">

            <div class="col-lg-12 mt-lg-n10 mb-5">
              <div class="card">
                <div class="card-header bg-green">
                  <div class="small text-white font-weight-500">
                    Create Article
                  </div>
                </div>
                <div class="card-body">
                  <form>
                    <div class="mb-3 g-3 row">
                      <div class="col-md-3">
                        <select v-model="author" class="form-select form-select-sm">
                          <option :value="null">Choose Author</option>
                          <option v-for="authors in AuthorName" v-bind:key="authors.value" :value="authors">{{authors.text}}</option>
                        </select>
                      </div>
                      <div class="col-md-6">
                        <input class="form-control form-control-sm" v-model="title" placeholder="Title" />
                      </div>
                      <div class="col-md-3">
                        <input class="form-control form-control-sm" id="image" type="file" />
                      </div>
                      <div class="col-md-12">
                        <textarea class="form-control form-control-sm" v-model="shortIntro" type="text" placeholder="Add Short Intro. 30-50 words (approx)"></textarea>
                      </div>
                    </div>
                    <button type="button" class="btn btn-primary me-2" @click="createArticle">Create</button>
                    <button type="button" class="btn btn-green-soft me-2" @click="resetForm">Reset</button>
                    <div class="progress col-md-4 d-inline-flex">
                      <div id="progressbar" class="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" aria-valuemin="0" aria-valuemax="100" style="width:25%">
                        Progress: 0%
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>

            <div class="col-lg-12 mt-lg-n10">
              <div class="card">
                <div class="card-header bg-green">
                  <div class="small text-white font-weight-500">
                    Created Articles
                  </div>
                </div>
                <div class="card-body">
                  <table class="table table-hover table-sm text-center align-middle">
                    <thead>
                      <tr>
                        <th scope="col">SN</th>
                        <th scope="col" class="w-30">Title</th>
                        <th scope="col">Date</th>
                        <th scope="col">Author</th>
                        <th scope="col">Edit</th>
                        <th scope="col">Content</th>
                        <th scope="col">To Draft</th>
                        <th scope="col">Delete</th>
                      </tr>
                    </thead>
                    <tbody id="postlist">
                      <tr v-for="article in articles" v-bind:key="article.id">
                        <th scope="row">{{article.count}}</th>
                        <td class="text-start">{{article.title}}</td>
                        <td>{{article.date}}</td>
                        <td>{{article.author}}</td>
                        <td class="small">
                          <a @click="openModal(article.id)">
                            <i class="bi bi-pencil-fill"></i>
                          </a>
                        </td>
                        <td class="small">
                          <a @click="addContent(article.id)" href="javascript:void(0)">
                            <i class="bi bi-pencil-square"></i>
                          </a>
                        </td>
                        <td class="small">
                          <a @click="sendToDrafts(article.id)" href="javascript:void(0)">
                            <i class="bi bi-cursor-fill"></i>
                          </a>
                        </td>
                        <td class="small">
                          <a @click="deleteArticle(article.id)" href="javascript:void(0)">
                            <i class="bi bi-trash-fill"></i>
                          </a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>

          </div>
        </div>
      </main>
    </section>

    <div v-if="showModal">
      <transition name="fade">
        <div class="modal-mask" style="display: block">
          <div class="modal-dialog" role="document">
            <div class="card modal-content">
              <div class="card-header bg-green">
                <div class="small text-white font-weight-500">
                  Edit Article
                </div>
              </div>
              <div class="card-body">
                <form>
                  <div class="mb-3 row g-3 align-items-center">
                    <div class="col-12">
                      <select v-model="editArticle.author" class="form-select form-select-sm">
                        <option :value="null">Choose Author</option>
                        <option v-for='authors in AuthorName' v-bind:key='authors'>{{authors}}</option>
                      </select>
                    </div>
                    <div class="col-12">
                      <input class="form-control form-control-sm" v-model="editArticle.title" placeholder="Title" />
                    </div>
                    <div class="col-md-12">
                      <textarea class="form-control form-control-sm" v-model="editArticle.shortIntro" row="2" placeholder="Short Intro"></textarea>
                    </div>
                    <div class="col-12">
                      <button type="button" class="btn btn-primary me-2" @click="updateData(editArticle.id)">Update</button>
                      <button type="button" class="btn btn-green-soft" @click="closeModal">Close</button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </transition>
    </div>

  </main>

</template>

<script>
  import {
    db,
    storage,
    nowTimestamp
  } from '../../components/firebaseInit'
  import AdminNavBar from '../../components/admin/AdminNavBar'

  export default {
    name: 'Create',
    title: 'Create',
    components: {
      AdminNavBar,
    },
    data() {
      return {
        counter: 1,
        articles: [],
        editArticle: {
          id: null,
          author: null,
          title: null,
          shortIntro: null,
        },
        marked: null,
        selected: null,
        AuthorName: [],
        author: null,
        title: null,
        shortIntro: null,
        showModal: false
      }
    },
    created() {
      db.collection('posts').where("status", "==", 1).orderBy('timestamp', 'desc').get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          if (doc.data().popularity == 0) {
            this.marked = false
          } else {
            this.marked = true
          }
          const data = {
            'id': doc.id,
            'title': doc.data().title,
            'date': doc.data().date,
            'author': doc.data().author,
            'popularity': doc.data().popularity,
            'mark_id': this.marked,
            'count': this.counter
          }
          this.counter++
          this.articles.push(data)
        })
      })

      db.collection('authors').get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = {
            'value': doc.id,
            'text': doc.data().name
          }
          this.AuthorName.push(data)
        })
      })
    },
    methods: {
      slugify(string) {
        var slug = ""
        var titleLower = string.toLowerCase()
        slug = titleLower.replace(/e|||||||||||/gi, 'e')
        slug = slug.replace(/a|||||||||||||||||/gi, 'a')
        slug = slug.replace(/o|||||||||||||||||/gi, 'o')
        slug = slug.replace(/u|||||||||||/gi, 'u')
        slug = slug.replace(//gi, 'd')
        slug = slug.replace(/\s*$/g, '')
        slug = slug.replace(/\s+/g, '-')
        return slug;
      },
      watcher() {
        db.collection('posts').where("status", "==", 1).orderBy('timestamp', 'desc')
          .onSnapshot((querySnapshot) => {
            this.articles = []
            this.counter = 1
            querySnapshot.forEach((doc) => {
              const data = {
                'id': doc.id,
                'title': doc.data().title,
                'date': doc.data().date,
                'author': doc.data().author,
                'popularity': doc.data().popularity,
                'mark_id': this.marked,
                'count': this.counter
              }
              this.counter++
              this.articles.push(data)
            })
          })
      },
      createArticle() {
        let timestamp = nowTimestamp
        let options = {
          weekday: 'long',
          year: 'numeric',
          month: 'long',
          day: 'numeric'
        }
        let date = nowTimestamp.toDate().toLocaleDateString('en-US', options).split(' ').slice(1).join(' ')
        let imagelen = document.querySelector('#image').files.length

        if (imagelen == 0 || this.title == null || this.author == null || this.shortIntro == null) {
          alert("Enter Data first!")
        } else {
          let image = document.querySelector('#image').files[0]
          let imageName = +new Date + "-" + image.name
          let metadata = {
            contentType: image.type
          }
          let storageRef = storage.ref('images/' + imageName)
          storageRef.put(image, metadata).then((snapshot) => {
            var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
            //console.log('Upload is ' + progress + '% done')
            document.getElementById('progressbar').style.width = progress + "%"
            document.getElementById('progressbar').innerText = progress + "%"
            snapshot.ref.getDownloadURL().then((url) => {
              let slug = this.slugify(this.title)
              db.collection("posts").add({
                  status: 1,
                  author: this.author.text,
                  authorid: this.author.value,
                  title: this.title,
                  slug: slug,
                  timestamp: timestamp,
                  date: date,
                  content: "Start Creating Masterpiece...",
                  shortIntro: this.shortIntro,
                  popularity: 0,
                  imagename: imageName,
                  imageurl: url
                })
                .then(() => {
                  alert('Data Updated successfully')
                  this.watcher()
                  this.resetForm()
                })
                .catch((e) => {
                  console.log(e);
                })
            })
          })
        }
      },
      deleteArticle(id) {
        if (confirm("Are you Sure to DELETE?")) {
          db.collection("posts").doc(id).update({
              status: 4
            })
            .then(() => {
              alert("Sent to archive section of DRAFTS");
              this.watcher()
            })
            .catch((e) => {
              console.log(e);
            })
        }
      },
      sendToDrafts(id) {
        if (confirm("Are you Sure?")) {
          db.collection("posts").doc(id).update({
              status: 2
            })
            .then(() => {
              alert("Sent to Drafts");
              this.watcher()
            })
            .catch((e) => {
              console.log(e)
            })
        }
      },
      openModal(id) {
        this.showModal = true
        document.getElementsByTagName('body')[0].style = 'overflow: hidden;'
        db.collection('posts').doc(id).get().then((doc) => {
          this.editArticle.id = doc.id,
            this.editArticle.author = doc.data().author,
            this.editArticle.title = doc.data().title,
            this.editArticle.shortIntro = doc.data().shortIntro
        })
      },
      closeModal() {
        this.showModal = false
        document.getElementsByTagName('body')[0].style = ''
      },
      updateData(id) {
        db.collection("posts").doc(id).update({
            author: this.editArticle.author,
            tag1: this.editArticle.tag1,
            tag2: this.editArticle.tag2,
            title: this.editArticle.title,
            shortIntro: this.editArticle.shortIntro
          })
          .then(() => {
            alert("Successfully Updated");
            this.watcher()
          })
          .catch((e) => {
            console.log(e);
          })
      },
      addContent(id) {
        window.open("../editor.html?" + id, "_blank");
      },
      resetForm() {
        document.querySelector('#image').value = null
        this.title = null
        this.author = null
        this.shortIntro = null
        document.getElementById('progressbar').style.width = "25%"
        document.getElementById('progressbar').innerText = "Progress: 0%"
      },
    }
  }
</script>

<style scoped>
  .modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.5s ease;
  }

  .fade-enter-from,
  .fade-leave-to {
    opacity: 0;
  }
</style>