<template>

  <nav class="navbar navbar-marketing navbar-expand-lg shadow bg-white navbar-light fixed-top">
    <div class="container">
      <router-link class="navbar-brand text-black" to="/">
        <!-- <img src="../../assets/images/sb-logo.svg" alt="Blog logo" /> -->
        <span class="ms-3 font-weight-500">Everything Civil Admin</span>
      </router-link>
      <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
        <ul class="navbar-nav ms-md-auto" id="navbardata">
          <li v-if="isLoggedIn" class="nav-item">
            <a class="nav-link active">Welcome, <span class="font-weight-500">{{currentUser}}</span></a>
          </li>
          <li v-if="isLoggedIn" class="nav-item">
            <router-link class="nav-link active" aria-current="page" to="/admin/dashboard">Dashboard</router-link>
          </li>
          <li v-if="isLoggedIn" class="nav-item">
            <router-link class="nav-link" aria-current="page" to="published">Published</router-link>
          </li>
          <li v-if="isLoggedIn" class="nav-item">
            <router-link class="nav-link" aria-current="page" to="drafts">Drafts</router-link>
          </li>
          <li v-if="isLoggedIn" class="nav-item">
            <router-link class="nav-link" aria-current="page" to="create">Create</router-link>
          </li>
          <li v-if="isLoggedIn" class="nav-item">
            <a v-on:click="logout" class="nav-link" style="cursor: pointer;" aria-current="page">Logout</a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
  
</template>

<script>
import firebase from 'firebase'

export default {
  name: 'AdminNavBar',
  data() {
    return {
      isLoggedIn: false,
      currentUser: false
    }
  },
  created() {
    // if (firebase.auth().currentUser) {
    //   this.isLoggedIn = true;
    //   this.currentUser = firebase.auth().currentUser.email
    //   console.log("JK")
    // }

    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.isLoggedIn = true;
        this.currentUser = firebase.auth().currentUser.email.split('@')[0]
        //console.log(user.displayName);
      } else {
        this.$router.push('/')
      }
    })
  },
  methods: {
    logout: function() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.push('/')
        })
    }
  }
}
</script>

<style scoped>
.navbar-brand img {
  height: 3.5rem;
}

.navbar-light .navbar-brand {
    color: #dc392d;
}

a {
  color: #dc392d !important;
  text-decoration: none;
  background-color: transparent;
}

</style>